	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Ever since Java's inception, in 1995, people have been compiling languages that aren't Java to bytecode and deploying them on the JVM. Lately, we are seeing an explosion in JVM languages. This is partly, but not only, because of Java 7, the first JVM to ship with invokedynamic, which is a quantum leap in polyglot runtime implementation. This session explains why emerging language implementations are becoming more common and more feasible to implement on the JVM with satisfactory performance. It tells you how we got there and walks through the technology already in place (such as invokedynamic) and that which is in progress. The future for the JVM is bright indeed. Filmed at JAX London 2013.","meta":{"duration":"PT51M4S"},"presenter":{"name":null},"source":{"id":"OKJgMwTLmEY","provider":"youtube"},"slug":"marcus-lagergren-the-java-virtual-machine-is-over-the-polyglot-vm-is-here","tag":["JVM","Marcus Lagergreen","Java","invokedynamic","polyglot","bytecode","JAX London 2013"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/OKJgMwTLmEY/default.jpg","height":90,"width":120}},"title":"Marcus Lagergren: The Java Virtual Machine is Over - The Polyglot VM is here"}},{"node":{"description":"You've written applications for the JVM, using various frameworks and maybe even various languages. You understand how to rig up the CLASSPATH, get .class files to load, compile source, and set up an IDE. But you've always wanted a better understanding of the plumbing underneath. How does JVM bytecode work? How does the JVM itself work? This presentation walks you through JVM and JVM bytecode basics, with lots of examples of how to bend the JVM to your whims.\n\nCopyright © 2013 Oracle and/or its affiliates. Oracle® is a registered trademark of Oracle and/or its affiliates. All rights reserved. Oracle disclaims any warranties or representations as to the accuracy or completeness of this recording, demonstration, and/or written materials (the \"Materials\"). The Materials are provided \"as is\" without any warranty of any kind, either express or implied, including without limitation warranties of merchantability, fitness for a particular purpose, and non-infringement.","meta":{"duration":"PT50M34S"},"presenter":{"name":null},"source":{"id":"rPyqB1l4gko","provider":"youtube"},"slug":"jvm-bytecode-for-dummies-and-the-rest-of-us-too","tag":["Java","JavaOne","2012","JVM","bytecode","IDE","CLASSPATH"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/rPyqB1l4gko/default.jpg","height":90,"width":120}},"title":"JVM Bytecode for Dummies (and the Rest of Us Too)"}}]}},"pathContext":{"slug":"bytecode","tag":"bytecode"}}

